.App {
  font-family: sans-serif;
  text-align: center;
}

.clearfix {
  overflow: auto;
}

body {
  padding: 5px;
}

.Query {
  float: right;
}

.Filter {
  cursor: pointer;
}

table {
  border-collapse: collapse;
  border: 3px solid #000;
  margin-top: 10px;
}

tr {
  border-bottom: 1px solid red;
}

td {
  padding: 5px;
}

